import React from 'react';
import {useHistory} from "react-router-dom";

const Error = () => {

  const router = useHistory()


  return (
    <>
      <div className='error'>
        <section className='error__section'>
          <div className='error__text'>
            <h1 className='error__status'>
              404
            </h1>
            <h3 className='error__name'>
                
            </h3>
            <p className='error__subtitle'>
               ,    .<span className='error__br375'/> ,<span className='error__br768'/>   <br/>
                <span className='error__br375'/>    .
            </p>
            <p className='error__subtitle'>
               <span className='error__br375'/>  <span onClick={() => router.push('/')} className='main-color error__back'> </span>
            </p>
          </div>
        </section>
      </div>
    </>
  );
};

export default Error;
